<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Ingrese los datos</h2>
    <div>
      <table>
        <tbody>
          <tr>
            <td>Sexo</td>
            <td>
            <select v-model="genero">
              <option disabled value="">Please select one</option>
              <option>Hombre</option>
              <option>Mujer</option>
            </select>
          </td>
          </tr>
          <tr>
            <td>Peso</td>
            <td>
            <input type="text" v-model="input_peso">
            </td>
          </tr>
          <tr>
            <td>Estatura</td>
            <td>
             <input type="text" v-model="input_estatura">
            </td>
          </tr>
          <tr>
            <td>IMC</td>
            <td>{{input_imc}}</td>
          </tr>
          <tr>
            <td>IMC Clasificación</td>
            <td>{{input_imc_tipo}}
            </td>
          </tr>
          <tr>
            <td colspan="2">
            <button class="btn btn-primary" v-on:click="input_imc=input_peso/(input_estatura * input_estatura)">Calcular IMC</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div>
      {{addToAmethod()}}
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      input_peso: '',
      input_estatura: '',
      input_imc: 0.0,
      input_imc_tipo: '',
      genero: 'Hombre'
    }
  },
   methods: {
        addToAmethod: function(){
            console.log('addToAmethod');
            if (this.genero === 'Hombre'){
              if (this.input_imc === 0.0){
                this.input_imc_tipo='';
              }
              else if (parseFloat(this.input_imc) < 18.49){
                this.input_imc_tipo='Infrapeso';
              }
              else if (parseFloat(this.input_imc) >= 18.50 & parseFloat(this.input_imc)<=24.99){
                this.input_imc_tipo='Peso Normal';
              }
              else if (parseFloat(this.input_imc) >= 25 & parseFloat(this.input_imc)<=29.99){
                this.input_imc_tipo='Sobre Peso';
              }
              else if (parseFloat(this.input_imc) >= 30 & parseFloat(this.input_imc)<=34.99){
                this.input_imc_tipo='Obesidad Leve';
              }
              else if (parseFloat(this.input_imc) >= 35 & parseFloat(this.input_imc)<=39.99){
                this.input_imc_tipo='Obesidad Media';
              }
              else if (parseFloat(this.input_imc) >= 40){
                this.input_imc_tipo='Obesidad Morbida';
              }
              else {
                this.input_imc_tipo='';
              }
            }else{
              if (this.input_imc === 0.0){
                this.input_imc_tipo='';
              }
              else if (parseFloat(this.input_imc) < 18.49){
                this.input_imc_tipo='Infrapeso';
              }
              else if (parseFloat(this.input_imc) >= 18.50 & parseFloat(this.input_imc)<=24.99){
                this.input_imc_tipo='Peso Normal';
              }
              else if (parseFloat(this.input_imc) >= 25 & parseFloat(this.input_imc)<=29.99){
                this.input_imc_tipo='Sobre Peso';
              }
              else if (parseFloat(this.input_imc) >= 30 & parseFloat(this.input_imc)<=34.99){
                this.input_imc_tipo='Obesidad Leve';
              }
              else if (parseFloat(this.input_imc) >= 35 & parseFloat(this.input_imc)<=39.99){
                this.input_imc_tipo='Obesidad Media';
              }
              else if (parseFloat(this.input_imc) >= 40){
                this.input_imc_tipo='Obesidad Morbida';
              }
              else {
                this.input_imc_tipo='';
              }
            }
        }
   }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
table {
  margin: auto;
  border: 2px solid #42b983;
  border-radius: 3px;
  background-color: #fff;
}
th {
  background-color: #42b983;
  color: rgba(255,255,255,0.66);
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

td {
  background-color: #f9f9f9;
}

th, td {
  min-width: 120px;
  padding: 10px 20px;
}

th.active {
  color: #fff;
}

th.active .arrow {
  opacity: 1;
}
</style>



